<template>
  <div id="modal">

      <div class="modal is-active">
          <div class="modal-background"></div>
          <div class="modal-content">
              <div class="box">
                  <div class="level-right">
                      <div class="level-item">
                          <a class="delete" @click="$emit('close')"></a>
                      </div>
                  </div>
                  <slot>

                  </slot>
              </div>
          </div>
      </div>

  </div>
</template>

<script>
export default {
  name: "modal",

  data() {
    return {};
  }
};
</script>


