<template>
  <div id="dashboardlist">
          
    <div class="level">
      <div class="level-left">
        <div class="level-item">
          <div class="title">
            List View
          </div>
        </div>
      </div>
    </div>

    <div class="tile is-ancestor has-text-centered">
    
    <div class="tile is-parent">
      <div class="tile is-child box">
        <h4 class="title">Search</h4>
        <h2 class="subtitle">Subtitle</h2>
        <div class="content">
          <p>
            Welcome! Here’s a quick guide. <br>
            User reactions are classified from 0 to 4 where <br>
            0 - Negative <br>
            1 - Somewhat Negative <br>
            2 - Neutral <br>
            3 - Somewhat Positive <br>
            4 - Positive
          </p>
          <button class="button is-primary is-fullwidth" @click="showModalSearch">Search</button>
        </div>
      </div>
    </div>

    <Modal v-show="showSearchModel" @close="showModalSearch">
      
      <Tile
      classNameParent="tile is-parent"
      classNameChild="tile is-child box">
      <h4 class="title">Search</h4>
      <h2 class="subtitle">
      </h2>
      <div class="content">

        <Textbox className="input" type="text" placeholder="" :data.sync="searchInput"></Textbox>
        
        <a class="button is-primary" @click="tweetsData">
          <span class="icon">
            <i class="fa fa-twitter"></i>
          </span>
          <span>Twitter</span>
        </a>

        <a class="button is-info" @click="postsData">
          <span class="icon">
            <i class="fa fa-facebook"></i>
          </span>
          <span>Facebook</span>
        </a>

        <p v-if="EnterData" class="help is-danger has-text-centered">Please enter any data.</p>
      
      </div>
      </Tile>

    </Modal>

      </div>
    
    <ul id="example-1">
      <li v-for="test in tests">
        <div class="tile is-ancestor">

          <Tile
          classNameParent="tile is-parent"
          classNameChild="tile is-child box">
          <h4 class="title">Postscores : {{test.Postscores}}</h4>
          <h2 class="subtitle">Creation Time : {{test.PostCreationTime}}</h2>
          <div class="content">
            {{test.Message}}
          </div>
          </Tile>
        
        </div>
      </li>
    </ul>
      
  </div>
</template>

<script>
import Modal from "@/components/Modal";
import Tile from "@/components/Tile";
import Textbox from "@/components/Textbox";

export default {
  name: "dashboardlist",

  data() {
    return {
      EnterData: false,
      tests: [],
      showSearchModel: false,
      showConnectModel: false,

      searchInput: ""
    };
  },

  methods: {
    showModalSearch() {
      this.showSearchModel = !this.showSearchModel;
    },
    tweetsData() {
      this.loading = true;

      axios
        .post("getTweets", {
          screen_name: this.searchInput
        })
        .then(response => {
          this.loading = false;
          if (response.data.data.msg == "Field Missing") {
            // status_code = 400 && data.msg = Field Missing
            // console.log(response.data);
            this.EnterData = true;
          } else {
            // console.log(response);
            this.EnterData = false;
            this.tests = response.data.data.Post;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    postsData() {
      this.loading = true;

      axios
        .get("getDateWiseScores", {
          params: {
            name: this.searchInput
          }
        })
        .then(response => {
          this.loading = false;
          if (response.data.data.msg.length == 0) {
            // status_code = 404 && data.msg = No Data Found
            // console.log(response.data);
            this.EnterData = true;
          } else {
            this.EnterData = false;
            this.tests = response.data.data.msg;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },

  components: {
    Modal,
    Tile,
    Textbox
  },

  beforeCreate() {}
};
</script>


