<template>
	<div id="textbox">

		<div class="field">
			<!-- <label class="label">{{label}}</label> -->
			<div class="control">
				<input
        :name="name"
        :type="type"
        :placeholder="placeholder" 
        :class="className"
        :v-validate="validate"
        :value="data" @input="$emit('update:data', $event.target.value)">
			</div>
		</div>

	</div>
</template>

<script>
export default {
  name: "textbox",

  props: [
    "label",
    "name",
    "type",
    "placeholder",
    "className",
    "validate",
    "data"
  ]
};
</script>

<style lang="scss">
#textbox {
  margin-top: 2rem;
  margin-bottom: 1rem;

  .input {
    border-radius: 40px;
    font-size: 20px;
    padding-left: 15px;
    color: #95a5a6;
  }
}
</style>


