<template>
  <div id="dashboardhome">
    
    <div class="level">
      <div class="level-left">
        <div class="level-item">
          <div class="title">
            Dashboard
          </div>
        </div>
      </div>
    </div>

    <div class="tile is-ancestor has-text-centered">
        
        <div class="tile is-parent">
          <div class="tile is-child box">
            <h4 class="title">Search</h4>
            <h2 class="subtitle">Subtitle</h2>
            <div class="content">
              <p>
                Welcome! Here’s a quick guide. <br>
                User reactions are classified from 0 to 4 where <br>
                0 - Negative <br>
                1 - Somewhat Negative <br>
                2 - Neutral <br>
                3 - Somewhat Positive <br>
                4 - Positive
              </p>
              <button class="button is-primary is-fullwidth" @click="showModalSearch">Search</button>
            </div>
          </div>
        </div>

        <Modal v-show="showSearchModel" @close="showModalSearch">
          
          <Tile
          classNameParent="tile is-parent"
          classNameChild="tile is-child box">
          <h4 class="title">Search</h4>
          <h2 class="subtitle">
          </h2>
          <div class="content">

            <Textbox className="input" type="text" placeholder="" :data.sync="searchInput"></Textbox>
            
            <a class="button is-primary" @click="tweetsData">
              <span class="icon">
                <i class="fa fa-twitter"></i>
              </span>
              <span>Twitter</span>
            </a>

            <a class="button is-info" @click="postsData">
              <span class="icon">
                <i class="fa fa-facebook"></i>
              </span>
              <span>Facebook</span>
            </a>

            <p v-if="EnterData" class="help is-danger has-text-centered">Please enter any data.</p>
          
          </div>
          </Tile>

        </Modal>

      </div>

      <div class="tile is-ancestor">

        <div class="tile is-vertical is-3">
        </div>
      
        <div class="tile is-vertical is-6">
          
          <div class="tile" v-if="ShowChart">

            <Tile
            classNameParent="tile is-parent"
            classNameChild="tile is-child box">
            <h4 class="title has-text-centered">Group Wise Scores</h4>
            <!-- <h2 class="subtitle">Subtitle</h2> -->
            <div class="content">
              <chart :type="'pie'" :data="pieData"></chart>
            </div>
            </Tile>
          
          </div>
          
        </div>
    
    </div>

  </div>
</template>

<script>
import Chart from "vue-bulma-chartjs";
import Modal from "@/components/Modal";
import Tile from "@/components/Tile";
import Textbox from "@/components/Textbox";

export default {
  name: "introduction",

  data() {
    return {
      EnterData: false,
      ShowChart: false,

      showSearchModel: false,
      showConnectModel: false,

      searchInput: "",

      getTweetsLabel: [],
      getTweetsData: [],
      getTweetsbackgroundColor: [
        "#FF6384",
        "#36A2EB",
        "#FFCE56",
        "#4BC0C0",
        "#FF9F40"
      ]
    };
  },

  methods: {
    showModalSearch() {
      this.showSearchModel = !this.showSearchModel;
    },
    tweetsData() {
      this.loading = true;

      axios
        .get("getTweetsData", {
          params: {
            name: this.searchInput
          }
        })
        .then(response => {
          this.loading = false;
          if (response.data.data.msg == "No Data Found") {
            // status_code = 404 && data.msg = No Data Found
            // console.log(response.data);
            this.EnterData = true;
          } else {
            // console.log(response.data);
            this.EnterData = false;
            this.getScore(response.data.data.msg);
            this.ShowChart = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    postsData() {
      this.loading = true;

      axios
        .get("getGroupWiseScores", {
          params: {
            name: this.searchInput
          }
        })
        .then(response => {
          this.loading = false;
          if (response.data.data.msg.length == 0) {
            // status_code = 404 && data.msg = No Data Found
            // console.log(response.data);
            this.EnterData = true;
          } else {
            this.EnterData = false;
            this.getScore(response.data.data.msg);
            this.ShowChart = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getScore(getScores) {
      for (let i = 0; i < getScores.length; i++) {
        // console.log(getScores[i]._id + " " + getScores[i].count);
        this.getTweetsData.push(getScores[i].count);
        this.getTweetsLabel.push(getScores[i]._id);
      }
    }
  },

  computed: {
    pieData() {
      return {
        labels: this.getTweetsLabel,
        datasets: [
          {
            data: this.getTweetsData,
            backgroundColor: this.getTweetsbackgroundColor
          }
        ]
      };
    }
  },

  components: {
    Chart,
    Modal,
    Tile,
    Textbox
  }
};
</script>

<style lang="scss">
#introduction {
  .tile.is-ancestor {
    margin-top: 2rem;
  }

  .title {
    margin-bottom: 2rem;
  }
}
</style>


