<template lang="html">
	<div id="login">

				<div class="columns">

					<div class="column is-8">
					</div>
					
					<div class="column is-4">

						<div class="userCheckEmail" v-if="userCheckEmail">
              <h1 class="title has-text-centered">Imprint</h1>
              <h1 class="avatar has-text-centered section">
                <img src="../assets/user.jpg" height="128" width="128">
              </h1>
							<Textbox name="email" type="email" placeholder="E-mail : smith@example.com" className="input" validate="required" :data.sync="email"></Textbox>
							<button class="button is-primary is-outlined is-large is-fullwidth" :disabled='loading' @click="checkEmail">Next</button>
              <p v-if="EnterEmailError" class="help is-danger has-text-centered">Please enter an e-mail.</p>
              <!-- <div class="section forgot-password">
                <p class="has-text-centered">
                  <a href="#">Forgot password</a>
                </p>
              </div> -->
              <!-- <input v-validate="'required|email'" :class="{'input': true, 'is-danger': errors.has('email') }" name="email" type="text">
              <span v-show="errors.has('email')" class="help is-danger has-text-centered">{{ errors.first('email') }}</span> -->
						</div>

            <Modal v-show="showEmailErrorModel">

              <h4 class="title">Search</h4>

            </Modal>

						<div class="userLogin" v-if="userLogin">
							<h1 class="title has-text-centered">Welcome</h1>
							<h1 class="subtitle is-5 has-text-centered">{{ email }}</h1>
							<Textbox name="password" type="password" placeholder="Password : ●●●●●●●" className="input" validate="required" :data.sync="password"></Textbox>
							<button class="button is-primary is-outlined is-large is-fullwidth" :disabled='loading' @click="login">Next</button>
              <p v-if="EnterPasswordError" class="help is-danger has-text-centered">Please enter a password.</p>
              <p v-if="WrongPasswordError" class="help is-danger has-text-centered">Wrong Password. Please try again.</p>
						</div>

						<div class="userRegistration" v-if="userRegistration">
							<h1 class="title has-text-centered">Couldn't find your Account</h1>
							<h1 class="subtitle is-6 has-text-centered">Would you like to create one</h1>
							<Textbox name="password" type="password" placeholder="Password : ●●●●●●●" className="input" validate="required" :data.sync="password"></Textbox>
							<Textbox name="confirm_password" type="password" placeholder="Confirm Password : ●●●●●●●" className="input" validate="required" :data.sync="confirm_password"></Textbox>
							<button class="button is-primary is-outlined is-large is-fullwidth"  :disabled='loading' @click="user_registration">Next</button>
              <p v-if="PasswordMissingError" class="help is-danger has-text-centered">Please enter missing fields.</p>
              <p v-if="PasswordMismatchError" class="help is-danger has-text-centered">Your password and confirm password don't match. Please try again.</p>
						</div>

					</div>

				</div>

	</div>
</template>

<script>
import Navbar from "@/components/Navbar";
import Textbox from "@/components/Textbox";
import Bottom from "@/components/Bottom";
import Modal from "@/components/Modal";

export default {
  name: "login",

  data() {
    return {
      loading: false,
      userCheckEmail: true,
      EnterEmailError: false,
      showEmailErrorModel: false,

      userLogin: false,
      EnterPasswordError: false,
      WrongPasswordError: false,

      userRegistration: false,
      PasswordMismatchError: false,
      PasswordMissingError: false,

      email: "",
      password: "",
      confirm_password: "",
      tests: {}
    };
  },

  methods: {
    checkEmail() {
      this.loading = true;

      axios
        .post("checkEmail", {
          email: this.email
        })
        .then(response => {
          // console.log(response.data);
          this.loading = false;
          if (response.data.data.msg == "Email Found") {
            // status_code = 200 && data.msg = Email Found
            // console.log(response.data);
            this.userCheckEmail = false;
            this.userRegistration = false;
            this.userLogin = true;
          } else if (response.data.data.msg == "Email Not Found") {
            // status_code = 404 && data.msg = Email Not Found
            // console.log(response.data);
            this.userCheckEmail = false;
            this.userLogin = false;
            this.userRegistration = true;
          } else if (response.data.data.msg == "Email Field Missing") {
            // status_code = 400 && data.msg = Email Field Missing
            // console.log(response.data);
            this.EnterEmailError = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    login() {
      this.loading = true;

      axios
        .post("login", {
          email: this.email,
          password: this.password
        })
        .then(response => {
          // console.log(response.data);
          this.loading = false;
          if (response.data.data.msg == "Login Data Found") {
            // status_code = 200 && data.msg = Login Data Found
            // console.log(response.data);
            this.$router.push("/dashboard");
          } else if (response.data.data.msg == "Password Must Match") {
            // status_code = 404 && data.msg = Password Must Match
            // console.log(response.data);
            this.EnterPasswordError = false;
            this.WrongPasswordError = true;
          } else if (response.data.data.msg == "Field Missing") {
            // status_code = 400 && data.msg = Field Missing
            // console.log(response.data);
            this.WrongPasswordError = false;
            this.EnterPasswordError = true;
          } else if (response.data.data.msg == "No Data Found") {
            // status_code = 404 && data.msg = No Data Found
            // console.log(response.data);
            this.userCheckEmail = false;
            this.userLogin = false;
            this.userRegistration = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    user_registration() {
      this.loading = true;

      axios
        .post("user_registration", {
          email: this.email,
          password: this.password,
          confirm_password: this.confirm_password
        })
        .then(response => {
          // console.log(response.data);
          this.loading = false;
          if (response.data.data.msg == "Successfully Register") {
            // status_code = 200 && data.msg = Successfully Register
            // console.log(response.data);
            this.$router.push("/dashboard");
          } else if (response.data.data.msg == "Email Already Register!!!") {
            // status_code = 400 && data.msg = Email Already Register!!!
            this.userCheckEmail = false;
            this.userRegistration = false;
            this.userLogin = true;
          } else if (response.data.data.msg == "Some fields are missing") {
            // status_code = 400 && data.msg = Some fields are missing
            this.PasswordMismatchError = false;
            this.PasswordMissingError = true;
          } else if (
            response.data.data.msg == "Password and confirm password must match"
          ) {
            // status_code = 400 && data.msg = Password and confirm password must match
            this.PasswordMissingError = false;
            this.PasswordMismatchError = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },

  components: {
    Navbar,
    Textbox,
    Bottom,
    Modal
  }
};
</script>

<style lang="scss">
#login {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

  .columns {
    margin: 0;

    .column.is-8 {
      min-height: 100vh;
      background: url("../assets/william-iven-22449.jpg");
      background-position: center;
      background-size: cover;
      background-blend-mode: normal;
    }

    .column.is-4 {
      .userCheckEmail {
        margin-top: 3rem;
      }

      .userLogin {
        margin-top: 14rem;
      }

      .userRegistration {
        margin-top: 12rem;
      }

      .avatar img {
        border-radius: 100px;
        padding: 5px;
        border: 1px solid #dbdbdb;
        height: 128;
        width: 128;
      }

      .button {
        border-radius: 40px;
        font-weight: bold;
      }

      // .forgot-password a {
      //   color: #95a5a6;
      //   font-weight: bold;
      //   padding-right: 20px;
      // }
    }
  }
}
</style>


